
import XImg from 'vux/src/components/x-img/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { mapGetters } from 'vuex'
export default {
    components: {
        XImg,
        dateFormat
    },
    filters: {
        filterLoc: function (val) {
            return val.toString();
        },
        filterComment: function (val) {
            let arr = [];
            for (let i in val) {
                arr.push(i);
            }
            return arr.length;
        },
        filterDate: function (val) {
            let now = Date.parse(new Date()) / 1000;
            console.log(now - val);
            if ((now - val) < 600 && (now - val) >= 60) {
                return "1分钟前"
            } else if ((now - val) < 1200 && (now - val) >= 600) {
                return "5分钟前"
            } else if ((now - val) < 6000 && (now - val) >= 1200) {
                return "10分钟前"
            } else if ((now - val) < 12000 && (now - val) >= 6000) {
                return "1小时前"
            } else if ((now - val * 1) < 120000 && (now - val) >= 12000) {
                return "2小时前"
            } else if ((now - val * 1) < 240000 && (now - val) >= 120000) {
                return "1天前"
            } else if ((now - val * 1) && (now - val) >= 240000) {
                return "2天前"
            } else {
                return dateFormat(val * 1000, 'MM-DD HH:mm')
            }

        }
    },
    created() {
            this.$store.dispatch('get_circles_friend_list', {begin: 0,offset: 100,isfriend: 1,uid: localStorage.getItem('loginopenid')})
    },
    computed: {
        ...mapGetters([
            'circles_friend_list'
        ])
    },
    methods: {
        //图片加载成功和失败
        success(src, ele) {
            const span = ele.parentNode.querySelector('span')
            ele.parentNode.removeChild(span)
        },
        error(src, ele, msg) {
            const span = ele.parentNode.querySelector('span')
            span.innerText = '加载失败。。。'
        }
    },
    data() {
        return {
            options: {
                getThumbBoundsFn(index) {
                    // find thumbnail element
                    let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]
                    // get window scroll Y
                    let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
                    // optionally get horizontal scroll
                    // get position of element relative to viewport
                    let rect = thumbnail.getBoundingClientRect()
                    // w = width
                    return { x: rect.left, y: rect.top + pageYScroll, w: rect.width }
                    // Good guide on how to get element coordinates:
                    // http://javascript.info/tutorial/coordinates
                }
            }
        }
    }
}




